.services_wrapper{
    width: 100%;
    min-height: 100vh;
    .services_homepage{
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        h1{
            color: #fff;
            font-size: 48px;
        }
    }
    .services{
        width: 100%;
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: var(--background_secondary);
        h3{
            width: 60%;
            text-align: left;
            color: #fff;
        }
        .services_container{
            width: 60%;
            height: fit-content;
            padding: 30px 0px;
            position: relative;
            z-index: 2;
            .flexbox{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-flow: row;
                flex-wrap: wrap;
                .service{
                    min-width: 300px;
                    width: 40%;
                    margin: 20px 0px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid var(--borders);
                    padding: 10px 20px 30px 20px;
                    background: var(--background_secondary);
                    svg{
                        width: 48px;
                        height: 48px;
                    }
                    .service_heading{
                        font-size: 16px;
                        font-weight: 600;
                        margin: 10px 0px;
                        color: #fff;
                    }
                    .service_desc{
                        color: var(--p);
                        font-size: 14px;
                        text-align: center;
                    }
                }
            }
        }
    }
}